'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { createClient } from '@/lib/supabase/client'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import Link from 'next/link'
import { Plus, LogOut, BookOpen, Home } from 'lucide-react'

export const dynamic = 'force-dynamic'

export default function TeacherDashboard() {
  const [user, setUser] = useState<any>(null)
  const [classrooms, setClassrooms] = useState<any[]>([])
  const [showNewClass, setShowNewClass] = useState(false)
  const [newClassName, setNewClassName] = useState('')
  const [newClassDesc, setNewClassDesc] = useState('')
  const [loading, setLoading] = useState(true)
  const [creating, setCreating] = useState(false)
  const router = useRouter()

  useEffect(() => {
    async function loadData() {
      const supabase = createClient()
      try {
        const {
          data: { user: authUser },
          error: userError,
        } = await supabase.auth.getUser()

        if (userError || !authUser) {
          router.push('/auth/login')
          return
        }

        setUser(authUser)

        // Check if teacher is in trial mode
        const { data: profile } = await supabase
          .from('profiles')
          .select('teacher_mode, role')
          .eq('id', authUser.id)
          .single()

        // Redirect to trial dashboard if in trial mode
        if (profile?.teacher_mode === 'trial') {
          router.push('/teacher/trial')
          return
        }

        // Redirect if not a teacher
        if (profile?.role !== 'teacher') {
          router.push('/protected')
          return
        }

        // Load teacher classrooms
        const { data: classData, error: classError } = await supabase
          .from('classrooms')
          .select('*')
          .eq('teacher_id', authUser.id)

        if (classError) throw classError
        setClassrooms(classData || [])
      } catch (err: any) {
        console.error('[v0] Error loading data:', err)
      } finally {
        setLoading(false)
      }
    }

    loadData()
  }, [])

  async function handleCreateClass() {
    if (!newClassName.trim()) return

    setCreating(true)

    const supabase = createClient()
    try {
      // Generate random class code
      const code = Math.random().toString(36).substring(2, 8).toUpperCase()

      const { data, error } = await supabase
        .from('classrooms')
        .insert({
          teacher_id: user.id,
          name: newClassName,
          description: newClassDesc,
          code,
        })
        .select()

      if (error) throw error

      setNewClassName('')
      setNewClassDesc('')
      setShowNewClass(false)
      setClassrooms([...classrooms, data[0]])
    } catch (err: any) {
      console.error('Error creating class:', err)
    } finally {
      setCreating(false)
    }
  }

  async function handleSignOut() {
    const supabase = createClient()
    await supabase.auth.signOut()
    router.push('/auth/login')
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <p className="text-muted-foreground">Loading...</p>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-black">
      <header className="border-b border-white/10 bg-black/80 backdrop-blur-xl sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 py-3 flex items-center justify-between h-16">
          <Link href="/protected/teacher" className="flex items-center gap-2 group">
            <div className="relative w-10 h-10 flex-shrink-0 rounded-lg bg-gradient-to-br from-cyan-500 to-blue-600 flex items-center justify-center shadow-lg shadow-cyan-500/30 group-hover:shadow-cyan-500/50 transition-all duration-300">
              <Home className="w-5 h-5 text-white" />
            </div>
            <div className="hidden sm:block">
              <h1 className="text-sm font-bold text-white leading-tight">ACL Teacher</h1>
              <p className="text-xs text-white/50">Portal</p>
            </div>
          </Link>
          <div className="flex items-center gap-2">
            <Link href="/teacher/join">
              <Button variant="outline" className="gap-2 border-white/10 text-white/80 hover:bg-white/5 bg-transparent text-sm">
                <BookOpen className="w-4 h-4" />
                <span className="hidden sm:inline">Join Class</span>
              </Button>
            </Link>
            <Button onClick={handleSignOut} variant="outline" className="gap-2 border-white/10 text-white/80 hover:bg-white/5 bg-transparent text-sm">
              <LogOut className="w-4 h-4" />
              <span className="hidden sm:inline">Sign Out</span>
            </Button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 py-8 space-y-8">
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-xl font-semibold text-white">Your Classes</h2>
            <p className="text-sm text-white/60">Manage your coding classrooms</p>
          </div>
          <Button
            onClick={() => setShowNewClass(!showNewClass)}
            className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-400 hover:to-blue-400 text-white gap-2"
          >
            <Plus className="w-4 h-4" />
            {showNewClass ? 'Cancel' : 'New Class'}
          </Button>
        </div>

        {showNewClass && (
          <Card className="border-cyan-500/20 bg-gradient-to-br from-cyan-500/10 to-blue-500/10 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-white">Create New Class</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label htmlFor="className" className="text-white">Class Name</Label>
                <Input
                  id="className"
                  placeholder="e.g., Introduction to Python"
                  value={newClassName}
                  onChange={(e) => setNewClassName(e.target.value)}
                  disabled={creating}
                  className="bg-black border-white/10 text-white"
                />
              </div>
              <div>
                <Label htmlFor="classDesc" className="text-white">Description (optional)</Label>
                <Input
                  id="classDesc"
                  placeholder="Brief description of the class"
                  value={newClassDesc}
                  onChange={(e) => setNewClassDesc(e.target.value)}
                  disabled={creating}
                  className="bg-black border-white/10 text-white"
                />
              </div>
              <div className="flex gap-2">
                <Button
                  onClick={handleCreateClass}
                  className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-400 hover:to-blue-400 text-white"
                  disabled={creating || !newClassName.trim()}
                >
                  {creating ? 'Creating...' : 'Create Class'}
                </Button>
              </div>
            </CardContent>
          </Card>
        )}

        <div>
          {classrooms.length === 0 ? (
            <Card className="border-dashed border-2 border-white/20 bg-white/5">
              <CardContent className="flex items-center justify-center py-12">
                <p className="text-white/60">No classes yet. Create your first class!</p>
              </CardContent>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {classrooms.map((classroom) => (
                <Card key={classroom.id} className="border-white/10 bg-white/5 hover:border-cyan-500/50 transition-all backdrop-blur-sm">
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <div>
                        <CardTitle className="text-white">{classroom.name}</CardTitle>
                        <CardDescription className="text-white/60">{classroom.description}</CardDescription>
                      </div>
                      <div className="bg-cyan-500/20 px-3 py-1 rounded text-sm font-mono text-cyan-300 border border-cyan-500/30">
                        {classroom.code}
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    <Button
                      asChild
                      onClick={() => router.push(`/teacher/classroom/${classroom.id}`)}
                      className="w-full bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-400 hover:to-blue-400 text-white"
                    >
                      <Link href={`/teacher/classroom/${classroom.id}`}>Manage Class</Link>
                    </Button>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </div>
      </main>
    </div>
  )
}
